.cntls-container {
  background-color: var(--vc-tertiary-bg-color);
  overflow-y: auto;
  height: 100%;
  border-radius: 5px;
}

.cntls-container.cntls-minimized {
  width: 75px !important;
}

.cntls-header > img {
  padding: 20px 30px 20px 15px;
  cursor: pointer;
}

.cntls-content {
  border-left: 1px solid var(--vc-secondary-bg-color);
  height: 89%;
  overflow-y: auto;
  background: white;
  border-radius: 5px;
}

.cntls-steps {
  color: var(--vc-primary-bg-color);
  width: 100%;
  padding: 10px 5px 5px 10px;
}

.cntls-steps > div {
  cursor: pointer;
}

.cntls-steps > div.selected {
  font-weight: 700;
}

.cntls-steps.cntls-minimized {
  margin-left: 10px;
}

.cntls-steps-footer {
  height: 25px;
}
.cntrol-steps {
  color: var(--vc-primary-bg-color);
  width: 100%;
  overflow-y: auto;
}

.cntrol-steps > div {
  cursor: pointer;
}

.cntrol-steps > div.selected {
  font-weight: 1000;
  color: rgb(12, 12, 206);
}
.cntrol-steps > div.revselected {
  font-weight: 1000;
  color: rgb(12, 12, 206);
}

.cntrol-steps.cntls-minimized {
  margin-left: 10px;
}

.cntrol-steps-footer {
  height: 25px;
}

.cntls-step {
  display: flex;
  align-items: center;
  margin: 0px 15px 0px 0px;
}

.cntls-step-icon {
  display: flex;
  align-items: center;
  border: 1px solid var(--vc-primary-bg-color);
  border-radius: 50%;
  margin-right: 10px;
  width: 32px;
  height: 32px;
}

.cntls-step-icon > img {
  width: 30px;
  height: 30px;
  padding: 5px;
  cursor: auto;
  filter: invert(74%) sepia(4%) saturate(10%) hue-rotate(195deg) brightness(92%)
    contrast(87%);
}

.cntls-step-label {
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cntls-separator {
  background-color: var(--vc-primary-bg-color);
  height: 15px;
  width: 1px;
  margin-left: 15px;
}

.cntls-step.cntls-processing > span > img {
  animation: rotate 1s linear infinite;
  filter: none;
}

.cntls-step.cntls-processing > span {
  color: var(--vc-accent-color);
}

@keyframes rotate {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}

.mapCard {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  justify-content: space-around;
}

.header-checkbox {
  align-items: center;
  justify-content: space-between;
}

.cntrl-header-items {
  background: white;
  padding:7px;
  border-bottom: 1px solid lightgray;
}

.cntls {
  width: 18%;
  margin: 11px 11px 0 0;
  height: 97%;
  
}

a.disabled {
  pointer-events: none;
  cursor: default;
  opacity: .5;
}

.p-sidebar-right .p-sidebar {
  height: auto;
}

.actions {
  font-size: 12px;
  padding-right: 5px;
}

div.item {
    vertical-align: top;
    display: inline-block;
    text-align: center;
    padding-right: 6px;
    cursor: pointer;
}

.checkbox-style {
  margin-right: 5px;
  margin-left: 0;
}

.p-accordion-header-text {
  text-transform: lowercase;
  font-size: 16px;
  font-weight: 500;
}

.p-accordion-header-text::first-letter{
  text-transform: uppercase;
}

.p-accordion .p-accordion-header .p-accordion-header-link {
  padding: 0.5rem!important;
  border: none!important;
  color: #50565D!important;
}

.ctrl-items {
  padding-top: 5px;
  padding-bottom: 5px;
}

.ctrl-items:nth-child(even) {
  background-color: #f1f8ff;
}

.p-accordion .p-accordion-content {
  padding: 0.5rem!important;
}

.clear-link {
  margin-left: auto;
  padding-right: 11px!important;
}

.p-accordion .p-accordion-tab {
  margin-bottom: 2px!important;
}

.selectComponentDiv {
  position: absolute;
  z-index: 99;
  background: white;
  width: 9%;
  padding: 15px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  top : 90px;
}

.component-checkbox label{
  padding-left: 35px;
}

.typeSectionToggle {
  cursor: pointer;
}

.cancelFIlterComponents {
  float: right;
  cursor: pointer;
}

.accordion-collapse-link, .accordion-expand-link {
  cursor: pointer;
  color: blue;
  font-size: 12px;
}

.accordion-collapse-link {
  margin-left: auto;
  padding-right: 10px;
}

.clear.disabled {
  opacity: .7;
  pointer-events: none;
  cursor: default;
  color: #50565D;
}