/*
* Device Viewer
*/
.dv {
  position: relative;
  top: 3px;
  width: 80%;
  margin: auto;
}

.dv-content {
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 4px var(--vc-primary-border-color);
  -webkit-box-shadow: 0 0 4px var(--vc-primary-border-color);
}

.dv-content .dv-processing {
  cursor: progress;
}

.dv-content > img {
  width: 100%;
}

.dv-content.dv-hidden {
  visibility: hidden;
}

.dv-selected-control {
  position: absolute;
  background: #4d00806a;
  padding: 5px;
  border: 2px solid rgb(99, 4, 143);
}


.dv-selected-control-hover {
  position: absolute;
  background: rgba(192, 54, 192, 0.166);
  padding: 5px;
  border: 3px dashed purple;
}

/* Progress bar */
.dv-pb-text {
  text-align: center;
  color: var(--vc-primary-bg-color);
  margin: -50px 20px 20px 20px;
  font-size: 18px;
  opacity: 0.8;
}

.dv-pb-container {
  background-color: var(--vc-secondary-bg-color);
  text-align: center;
  width: 228px;
  height: 6px;
  border-radius: 5px;
}

.dv-pb-content {
  background: var(--vc-accent-color);
  height: 6px;
  width: 80px;
  border-radius: 5px;
  animation: increase 3s infinite;
}

@keyframes increase {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(148px);
  }
  100% {
    transform: translateX(0);
  }
}

/* Keyboard controller */
.dv textarea {
  position: absolute;
  background-color: transparent;
  border: none;
  resize: none;
  width: 0px;
  height: 0px;
  overflow: hidden;
  z-index: -1;
}

.p-speeddial-direction-down {
  right: -32px!important;
  top: -37px!important;
}

.p-speeddial-direction-down .p-speeddial-list {
  flex-direction: row!important;
}

.p-speeddial-button.p-button.p-button-icon-only {
  width: 2rem!important;
  height: 2rem!important;
  padding: 0!important;
}

.p-speeddial-action {
  width: 2rem;
  height: 2rem;
}

.highlight-items {
  margin: "auto";
  display: none;
  justify-content: center;
}
