.d-flex {
    display: flex;
}

.form {
  align-items: center;
}

.coordinate-section, .content-section {
  width: 50%;
  padding: 10px;
}

.l-controlname,
.l-content,
.l-confidence {
  position: center;
  display: inline-block;
  color: rgb(21, 32, 128);
  display: inline;
  margin-top: 10px;
  width: 50%;
  font-size: 14px;
}

.l-coordinates {
  position: left;
  color: rgb(21, 32, 128);
  border-top-right-radius: 12px, 12px, 12px, 12px;
}

.l-x,
.l-y,
.l-w,
.l-h {
  padding: 12px 12px 12px 0;
  color: rgb(21, 32, 128);
  display: contents;
  right: 0%;
  top: 10%;
}

.confidence-input, .content-input, .controlname-input {
  width: 100%;
  height: 10%;
  padding: 5px 10px;
  margin: 5px;
}

.x-input,
.y-input,
.w-input,
.h-input {
  height: 9%;
  width: 21%;
  text-align: left;
  padding: 4px;
  margin: 5px 18px;
}

.control-details-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1em;
  background-color: var(--vc-primary-bg-color);
  color: var(--vc-white-color);
  padding-left: 15px;
  height: 40px;
  z-index: 1;
}

.btn {
  color: white;
  width: 10%;
  margin: 5px;
  font-size: medium;
  padding: 5px;
  border-radius: 15px;
}

.btn-section {
  justify-content: space-around;
  align-items: center;
}

.btn-section a {
  cursor: pointer;
  margin: 3px;
}

hr {
  margin: 0;
}

.coordinates {
  align-items: center;
}

label {
  font-size: 14px;
  margin-bottom: 0;
}

.p-sidebar .p-sidebar-header {
  padding: 10px;
}

.save {
  display: block;
  margin-right: auto;
  margin-left: auto;
  margin-top: 10px;
}

.moreLink {
  cursor: pointer;
  color: blue;
}

.moreSection {
  margin-top: 10px;
}
