.design-tb {
    background-color: var(--vc-tertiary-bg-color);
    overflow-y: auto;
    width: 15%;
    background-color: white;
    margin: 11px 0 14px 11px;
    border-radius: 5px;
}

.design-tb-content > div > TabPanel {
    cursor: pointer;
    color: var(--vc-accent-color);
    top: 500px;
}

.design-tb-content > div.selected {
    font-weight: 700;
    margin-top: 20px;
    color: rgb(12, 146, 7);
    
}
/* .design-tb-content > div.selected > img.thumbnail { */
div.selected > img.thumbnail {
    border: solid;
    border-color: rgb(218, 18, 18);
    border-width: 3px;
}

.p-inputgroup {
    border: 1px solid;
    width: 94%;
}

.p-inputtext {
    width: 100%;
}

.results-header{
    display: flex;
    align-items: center;
    font-size: medium;
    color: black;
    height: 40px;
    z-index: 1;
    margin: 8px 0px;
    padding-left: 14px;
}

.indexNumber {
    position: relative;
    left: 5px;
    top: 19px;
}

#selectedImage {
    text-align: right;
}

.thumbnail{
    border: solid;
    border-color: rgb(17, 3, 78);
    border-width: 3px;
    padding: 2px;
    margin-top: 5px;
    width: 165px;
    height: 119px;
}

.backupImg{
    position: absolute;
    font-size:xx-small;
    color:rgb(255, 255, 255);
    background-color: rgb(8, 95, 47);
    opacity: 0.8;
    height: 5%;
    width: 5%;
    position: center;
    font-size: medium;
    bottom: 85%;
    right: 30%;

}

.deleteImg {
    border: 0;
    position: relative;
    top: -108px;
    right: 12px;
    cursor: pointer;
    transition: 0.3s;
    background: #ff0000cc;
    color: white;
    border-radius: 50% 50%;
    padding: 3px 4px;
    opacity: 0;
}

.thumbnail:hover + .deleteImg, .deleteImg:hover{
    opacity: 1;
}

.deleteImageDialog {
    width: 18%;
}

