/* 
* Virtual Client
*/
.vc {
  display: flex;
  position: relative;
  flex-direction: column;
  background-color: var(--vc-white-color);
  height: 100vh;
  width: 100%;
  overflow-y: hidden;
}

.ac-pb {
  height: 100vh!important;
}

.vc-container {
  display: flex;
  height: 100%;
  overflow-y: hidden;
  background-color: var(--vc-tertiary-bg-color);
  font-size: 14px;
  background-color: #e7dfdf87;
}

.vc-content {
  width: 65%;
  height: 96%;
  display: flex;
  justify-content: center;
  overflow: hidden;
  padding: 10px;
  background-color: white;
  margin: 11px;
  border-radius: 5px;
}

.home-icon {
  cursor: pointer;
  font-size: 0.8rem!important;
}

.vc-error {
  margin-top: 10px;
  text-align: center;
}

.backLink {
  padding: 10px 11px;
  box-shadow: 0px 0px 1px 0px;
  margin-bottom: 3px;
}

.app-name{
  padding-left: 13px;
  font-weight: 600;
}

.imageprocessCard > .p-progressbar {
  width: 50%;
  margin: auto;
  top: 50%;
  bottom: 50%;
}

.imageprocessCard {
  height: 100%;
}